import { mapActions } from 'vuex';
import baseMixin from '@/mixins'
export default {
	name:'functionMenu',
	mixins:[baseMixin],
	data() {
		return {
		     allMenuList:[],//功能菜单列表
		}
	},






	onReady () {
		this.allMenuList = this.menuList
	},

	methods: {
		//跳转到指定页面
		turnToPage(_url){
			console.log('currentUrl:'+_url)
			switch (_url){
				case 'zx':
					uni.navigateTo({
						url: '/pages/verify/boxApproval/boxApproval'
					});
					break;
				case 'tax':
					uni.navigateTo({
						url: '/pages/verify/bargainPrice/bargainPrice'
					});
					break;
				case 'material':
				uni.navigateTo({
					url: '../verify/material/material'
				});
					break;
				case 'purchases':
				uni.navigateTo({
					url: '../verify/originalPaper/originalPaper'
				});
					break;
				case 'paperIn':
				uni.navigateTo({
					url: '../warehouse/paperIn/paperIn'
				});
				break;
				case 'boxIn':
				uni.navigateTo({
					url: '../warehouse/boxIn/boxIn'
				});
				break;
				case 'paperOB':
				uni.navigateTo({
					url: '../warehouse/paperOB/paperOB'
				});
					break;
				default:
				    uni.showToast({
				    	title:'找不到指定路径，请确认！'+_url,
						icon:'none',
						duration:2000
				    })
					break;
			}
		}
		
	}
}
